<template>
  <div id="buyAndSell">
    <div class="header">
      <van-row gutter="20">
        <van-col span="4" style="padding-right: 0px !important;padding-left: 0px;">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" @click="$router.back(-1)" style="color: #8b8b8b !important;vertical-align: middle;"></i>
        </van-col>
        <van-col span="16" class='search font_12' style="background:#ffffff;">
          <van-search v-model="value" placeholder="搜索" @search="getList1"/>
        </van-col>
        <van-col span="3" style="padding-left:0px !important;padding-right: 0px;">
          <div class="font_12" style="margin-right: -5px;" @click="toAddSellers">快捷发布</div>
        </van-col>
      </van-row>
    </div>
    <div class="body_box" style="padding: 5px">
      <van-list v-model="loading" :finished="finished" @load="getList">
        <div class="border_top_eeeeee background_color_ffffff" @click="toRoomInfo(item)" v-for="(item,index) in list">
          <div style="padding: 5px;height: 30px;">
            <img :src="item.user.userimg||'static/img/img_tx_mmersf.png'" width="30px" style="margin-right: 6px;vertical-align: text-top;float: left;"/>
            <span>{{item.user.username}}</span>
            <br>
            <span>
              <img src="static/img/vipp.png" v-if="item.user.primary=='Y'||item.user.senior=='Y'"/>
              <span class="font_12 color_fcba62" v-if="item.user.primary=='Y'||item.user.senior=='Y'">会员</span>
              <span class="float_right font_12 color_8b8b8b">发布时间:{{item.addtime}}</span>
            </span>
          </div>
          <van-card>
            <div slot="thumb">
              <img :src="item.headImg" v-lazy="item.headImg" width="100%" style="display: block;height:63px" />
            </div>
            <div slot="tags" class="font_weight_bold padding_bottom_5 font_14 goods_title">
              {{item.title}}
            </div>
            <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5">
              {{item.label}}
            </div>
            <div slot="tags" class="padding_bottom_5 font_12">
              ¥{{item.price}}
            </div>
            <div slot="tags" class="font_12 color_8b8b8b">
              <span>{{item.address}}</span>
            </div>
          </van-card>
        </div>
      </van-list>
      </div>
    </div>
</template>

<script>
  export default {
    data() {
      return {
        list:[],
        value:'',
        loading: false,
        finished: false,
        pageNum: 1,
      }
    },
    mounted() {
      this.getList();
    },
    methods: {
      toAddSellers(){
        this.$router.push('addSellers');
      },
      getList1(){
        this.pageNum = 1;
        this.list = [];
        this.loading = false;
        this.finished = false;
        this.getList();
      },
      getList(){
        this.loading = true;
        let $this = this;
        let obj = {
          search:this.value,
          type: 2,
          pageNum: $this.pageNum
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecitylist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.data.length>0){
              for(let i=0;i<rpdata.data.data.data.length;i++){
                if(rpdata.data.data.data[i].sameimg){
                  rpdata.data.data.data[i].headImg = rpdata.data.data.data[i].sameimg.split(',')[0];
                }
              }
              $this.list = $this.list.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else if(rpdata.data.data.data.length==0){
              $this.finished = true;
              $this.loading = false;
            }

            // $this.list = rpdata.data.data.data;
          }
        })
        .catch(function(err) {})
      },
      toRoomInfo(dataMap){
        this.$store.commit('setRoomInfo', dataMap);
        this.$router.push('roomInfo');
      },
    }
  }
</script>

<style>
  #buyAndSell .goods_title {
    height: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  #buyAndSell .van-card:not(:first-child) {
    margin-top: 5px;
  }
  #buyAndSell .van-card__thumb {
    top: 0px;
    left: 8px;
    width: 90px;
    height: 75px;
    position: absolute;
  }
  #buyAndSell .van-card {
    height: 80px;
  }
  #buyAndSell .body_box{
    margin-top: 50px;
  }
  #buyAndSell .van-search input {
    color: #8b8b8b;
  }
  #buyAndSell .typeList {
    text-align: center;
  }
  #buyAndSell .typeList span {
    font-size: 14px;
    color: rgb(102, 102, 102)
  }
  #buyAndSell .typeImg {
    display: block;
    width: 75%;
    margin: 10px auto;
  }
  #buyAndSell {
    width: 100%;
  }
  #buyAndSell .header {
    height: 44px;
    line-height: 44px;
    box-shadow: 0px 2px 10px lightgrey;
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
    background-color: white;
  }
  #buyAndSell .header img {
    vertical-align: text-top;
  }
  #buyAndSell .search {
    border: 1px solid lightgray;
    border-radius: 30px;
    margin-top: 6px;
  }
  #buyAndSell .header .van-col {
    text-align: center;
  }
  #buyAndSell .van-search {
    padding: 0px;
  }
  #buyAndSell .van-search {
    background-color: transparent!important;
  }
  #buyAndSell .swiper img {
    margin: 0 auto;
    display: block;
  }
  #buyAndSell .nav img {
    width: 100%;
    height: auto;
  }
  #buyAndSell .navCon span {
    font-size: 13px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
  }
  #buyAndSell .rightText div {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 16px;
  }
  #buyAndSell .footer .van-col div {
    text-align: center;
    font-size: 14px;
  }
  #buyAndSell .swiperTwo .van-swipe__indicators{
    display: none;
  }
  #buyAndSell .swiperTwo{
    margin-top: 44px;
  }
  #buyAndSell .van-field__control {
    font-size: 12px;
  }
</style>



